body {
    color: white;
    background: url('../Components/numeros.jpg')  center center;
    

}

.title {
    font-size:125px;
    font-weight:bold;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

label {
    font-size:10px;
    font-weight: bolder;
    padding: 0;
    margin: 0;

}

.blur {
    backdrop-filter: blur(10px)
}

.color {

    color: #0c063f;

}


::-webkit-scrollbar {
    width: 10px;
  }
  
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(34, 31, 31); 
    border-radius: 10px;
  }
   

::-webkit-scrollbar-thumb {
    background:#0c063f;
    border-radius: 10px;
  }
  
::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }
  
  